@import url("https://fonts.googleapis.com/css2?family=Recursive&display=swap");

.main {
    border-radius: 20px;
    width: fit-content;
    background-color: #01579b;
}

.top {
    height: 60px;
    background-color: #424242;
    color: whitesmoke;
    padding: 10px;
    border-radius: 20px 20px 0 0;
    font-family: "Recursive", sans-serif;
    display: flex;
    justify-content: center;
}

.header {
    background-color: #424242;
    color: whitesmoke;
    margin: 10px 0px 0px 10px;
    font-size: 25px;
    border-radius: 20px 20px 0 0;
    font-family: "Recursive", sans-serif;
}

.day {
    padding: 15px;
    color: whitesmoke;
    font-family: "Recursive", sans-serif;
    font-size: 24px;
    font-weight: 600;
}

.temp {
    padding: 15px;
    color: whitesmoke;
    font-family: "Recursive", sans-serif;
    font-size: 18px;
}

.flex {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.description {
    padding: 15px;
    color: whitesmoke;
    font-family: "Recursive", sans-serif;
    font-size: 24px;
    font-weight: 600;
}
.button {
    width: fit-content;
    border-radius: 2ch;
    height: 35px;
    margin: 8px 0 0 20px;
}
